export const alphaNumericValidator = value => (value && /[^a-zA-Z0-9 ]/i.test(value)
  ? 'Only alphanumeric characters'
  : undefined);

export const userNameValidator = value => (value && !/^[A-Za-z--' ]+$/i.test(value)
  ? 'Informe um nome com caracteres vlidos'
  : undefined);

export const emailValidator = value => (value && !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)
  ? 'Email invlido'
  : undefined);

export const requiredValidator = value => (value ? undefined : 'Campo obrigatrio');


export const requiredSelectValidator = value => ((value !== '0') ? undefined : 'Campo obrigatrio');


export const requiredHeaderNameValidator = value => (value ? undefined : 'Digite um nome para o novo cabealho');
