import React from 'react';
import { Field, reduxForm, Form } from 'redux-form';
import {
  Input, InputGroup, InputGroupAddon, Button, Row, Col, UncontrolledTooltip,
} from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const renderField = ({
  input,
  type,
  meta: { pristine },
  clearSearch,
  clearSearchField,
  search,
  placeholder,
}) => (
  <InputGroup>
    <Input
      {...input}
      placeholder={placeholder}
      type={type}
    />
    {search || !pristine ? (
      <InputGroupAddon addonType="prepend">
        <Button className="c-question-base__clear-search" id="dica" onClick={search ? clearSearch : clearSearchField}>
          <FontAwesomeIcon icon="times-circle" />
        </Button>
        <UncontrolledTooltip placement="bottom" target="dica" className="tooltip__message">
              Limpar busca
        </UncontrolledTooltip>
      </InputGroupAddon>
    ) : ''}
    <InputGroupAddon addonType="prepend">
      <Button type="submit">
        <FontAwesomeIcon icon="search" className="btn__icon" />
              Pesquisar
      </Button>
    </InputGroupAddon>
  </InputGroup>
);

const QuestionSearchForm = (props) => {
  const {
    handleSubmit, search, clearSearch, clearSearchField,
  } = props;

  return (
    <Form onSubmit={handleSubmit}>
      <Row>
        <Col sm="12" className="c-question-base__title d-flex justify-content-between">
          <div className="p-2" />
          <div className="p-2">
            <h4>
              Banco de Questes
              {' '}
            </h4>
          </div>
          <div className="p-2 c-question-base__l-tooltip">
            <span className="c-question-base__tooltip" href="#" id="TooltipExample">
              <FontAwesomeIcon icon="info-circle" />
            </span>
            <UncontrolledTooltip className="tooltip__message" placement="right" target="TooltipExample">
              Insira termos especficos sobre o que deseja encontrar - o sistema buscar nas tags e em todos os textos das questes. Ex: ngulos internos. Se desejar buscas mais abrangentes, separe os termos com vrgulas. Exemplo: polgonos, ngulos internos. Combine os termos da busca com as opes de filtro disponveis na barra lateral.
            </UncontrolledTooltip>
          </div>
        </Col>
      </Row>
      <Row className="c-question-base__search-text">
        <p className="c-question-base__search-info hidden">
          Pesquisar por palavras-chave no banco de questes
        </p>
        <Field
          component={renderField}
          type="text"
          name="searchText"
          id="searchText"
          placeholder="Pesquisar por palavras-chave no banco de questes"
          className="form-control"

          search={search}
          clearSearch={clearSearch}
          clearSearchField={clearSearchField}
        />
      </Row>
      {search ? (
        <Row>
          <Col sm="12">
            <p className="c-question-base__keywords-title">
              <span>Resultado da busca para:</span>
              <span className="c-question-base__keywords">
                {' '}
                {search}
              </span>
            </p>
          </Col>
        </Row>) : ''
      }
    </Form>
  );
};

export default reduxForm({
  form: 'questionSearch',
})(QuestionSearchForm);
