import { apiUrl } from 'helpers/config';


export const handleResponse = response => response.json().then((data) => {
  if (!response.ok) {
    /* temporary solution shows only first error */
    if (data.email) {
      if (data.email[0].includes('Um usurio j foi registado com este endereo de e-mail')) {
        return Promise.reject('Um usurio j foi registrado com este endereo de e-mail.');
      }
      return Promise.reject(data.email[0]);
    }
    return Promise.reject(data[Object.keys(data)[0]]);
  }

  return data;
});

function register(email, password, name, acceptTerms) {
  const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      username: email,
      email,
      password1: password,
      password2: password,
      name,
      terms_use: acceptTerms,
    }),
  };

  return fetch(`${apiUrl}/auth/registration/`, requestOptions)
    .then(handleResponse)
    .then(detail => detail);
}

function verifyEmail(key) {
  const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      key,
    }),
  };

  return fetch(`${apiUrl}/auth/registration/verify-email/`, requestOptions)
    .then(handleResponse)
    .then(detail => detail);
}

function resendEmail(email, password) {
  const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      username: email,
      email,
      password,
    }),
  };

  return fetch(`${apiUrl}/resend_confirmation_email`, requestOptions)
    .then(handleResponse)
    .then(detail => detail);
}

const registerService = {
  register,
  verifyEmail,
  resendEmail,
};
export default registerService;
